@charset "utf-8";

body {
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
    font-family: BlinkMacSystemFont,-apple-system,"Segoe UI",Roboto,Oxygen,
                Ubuntu,Cantarell,"Fira Sans","Droid Sans","Helvetica Neue",Helvetica,Arial,sans-serif;
    font-size: 14px;
}
input {
  font-size: 14px;
}
#helppanel {
  font-size: 14px;
}

@media (max-width: 768px) {
  body {
    font-size: 20px;
  }
  input {
    font-size: 20px;
  }
  #helppanel {
    font-size: 16px;
  }
}

:root {
  --perc-left: 1%;
  --width: 15%;
}

a { color: #a44; text-decoration: none; }
a:hover, a.enabled { color: #f00; }

#sketch-container {
    margin: 0;
    padding: 10px;
    position: fixed;
    top: 0%;
    left: 0px;
    width: calc(3*var(--perc-left) + 4*var(--width)); /*60%*/
    height: 60%;
    overflow: hidden;
}
#sketchrender, #sketchlive{
    position: absolute;
    cursor: crosshair;
    overflow: hidden;
}

#layers3D-container{
  margin: 0;
  padding: 10px;
  position: fixed;
  top: 0%;
  left: 70%;
  height: 60%;
  width:30%;
  overflow: hidden;
}


#sketch1{
  margin: 0;
  padding: 0px;
  position: fixed;
  top: 70%;
  left: var(--perc-left);
  width: 15%;
  height: 25%;
  overflow: hidden;
  border: 1px solid black;
}
#sketchrender1, #sketchlive1{
  position: absolute;
  cursor: crosshair;
  overflow: hidden;
}


#sketch2{
  margin: 0;
  padding: 0px;
  position: fixed;
  top: 70%;
  width: 15%;
  height: 25%;
  left: calc(2*var(--perc-left) + 1*var(--width));
  overflow: hidden;
  border: 1px solid black;
}
#sketchrender2, #sketchlive2{
  position: absolute;
  cursor: crosshair;
  overflow: hidden;
}


#sketch3{
  margin: 0;
  padding: 0px;
  position: fixed;
  top: 70%;
  width: 15%;
  height: 25%;
  left: calc(3*var(--perc-left) + 2*var(--width));
  overflow: hidden;
  border: 1px solid black;
}
#sketchrender3, #sketchlive3{
  position: absolute;
  cursor: crosshair;
  overflow: hidden;
}


#sketch4{
  margin: 0;
  padding: 0px;
  position: fixed;
  top: 70%;
  width: 15%;
  height: 25%;
  left: calc(4*var(--perc-left) + 3*var(--width));
  overflow: hidden;
  border: 1px solid black;
}
#sketchrender4, #sketchlive4{
  position: absolute;
  cursor: crosshair;
  overflow: hidden;
}

#sketch-UI {
    /*overflow-y: scroll;*/
    overflow-y: hidden;
    overflow-x: hidden;
    position: absolute;
    top:67%;

    right: 0px;   /*reset this to zero if needed - Varda*/
    z-index: 2;
    padding: 10px;
    left: calc(5*var(--perc-left) + 4*var(--width));
    width: calc(100 - (3*var(--perc-left) + 4*var(--width)));
    background: rgba(245,245,245,0.9);
}

.max-UI {  width: 250px;  }
@media (max-width: 768px) and (min-width:425px) { .max-UI {  width: 280px;  } }
@media (max-width: 425px) {                       .max-UI {  width: 100%    } }
.min-UI {  width: 320px;  }
@media (max-width: 768px) and (min-width:425px) { .min-UI {  width: 360px;  } }
@media (max-width: 425px) {                       .min-UI {  width: 100%    } }

@media (max-width: 358px) {
  .eslogo {display: none;}
  #sketch-UI .button {margin:0px;}
} /* HACK for tiny screens...*/

.UIheader {
  display:inline-block;
  font-variant: small-caps;
  font-weight: 500;
  margin:5px 0 5px 0;
  padding-top: 0.2rem;
  padding-right:100%;
  /*width: 100%;*/
  border-bottom: 1px solid;
  border-color: #ddd;
}

.button {
    border:1px solid #ddd;
    background: #eeeeee;
    text-indent:0px;
    text-align: center;
    border-radius: 2px;
    margin: 2px;
    padding: 3px;
    display: inline-block;
    cursor: pointer;
    color: #666;
    /* don't allow text selection */
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.button:hover, .button:focus {
	background: #ffdddd;
	border: solid 1px #dddddd;
}
.button:active {
	background: #aaaaaa;
  color:#333;
}
.selected {
    background: rgba(250,150,150,0.6);
}
.armed {
    background: rgba(255,0,0,0.6);
}

