/*Gradient slider*/
/* styles.css */
#slider-container {
  height: 60%;
  top: 10px;
  left: calc(5*var(--perc-left) + 4*var(--width));
  position: fixed;
}

#slider {
  height: 60%;
  width: 10px;
  background: linear-gradient(to bottom, hsl(0, 70%, 70%), hsl(60, 70%, 70%), hsl(120, 70%, 70%), hsl(180, 70%, 70%));
  border: none;
  outline: none;
  position: fixed;
}

.thumb {
  width: 20px;
  height: 20px;
  background-color: white;
  border-radius: 50%;
  border: 2px solid #333;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
}

.thumb:nth-child(1) {
  top: 0;
}

.thumb:nth-child(2) {
  top: 33%;
}

.thumb:nth-child(3) {
  top: 66%;
}

.thumb:nth-child(4) {
  top: 100%;
}

/* Updated styles for the tooltip */
.tooltip {
  position: absolute;
  top: 50%; /* Adjust top position */
  left: -30px; /* Adjust left position to the left of the slider */
  background: white;
  padding: 5px;
  font-size: 10px; /* Smaller font size */
  border: none;
  display: none;
}

#single-slider-container {
  top:67%;
  width:calc(3*var(--perc-left) + 4*var(--width));
  left: var(--perc-left);
  height: 6px; /* Set the desired height of the slider */
  position: fixed;
  background: #aaaaaa; /* Set the background color of the slider */
}

#single-slider-container .thumb {
  width: 15px; /* Set the width of the thumb */
  height: 15px; /* Set the height of the thumb */
  background-color: #e87d7d; /* Set the initial color of the thumb */
  border-radius: 50%; /* Make the thumb circular */
  left: 0%;
  /*position: absolute;*/
  top: 67%; /* Center the thumb vertically within the slider */
  transform: translate(-50%, -50%); /* Center the thumb horizontally within the slider */
  cursor: pointer;
}

#single-slider-container .tooltip {
  position: absolute;
  top: 67%; /* Adjust top position */
  left: -30px; /* Adjust left position to the left of the slider */
  background: white;
  padding: 5px;
  font-size: 10px; /* Smaller font size */
  border: none;
  display: none;
}



